




















































































































































































import Vue from 'vue';
import { configModule } from '@/store/modules/config';
import iconConfig from '@/store/misc/icons';

export default Vue.extend({
  name: 'Install',
  data() {
    return {
      loading: false,
      disabled: false,
      errorMessage: '',
      successMessage: '',
      iconConfig
    }
  },
  computed: {
    installEvent() {
      return configModule.installEvent;
    }
  },
  methods: {
    promptInstall() {
      const event = configModule.getInstallEvent;
      if (event) {
        this.loading = true;
        event.prompt();
        event.userChoice.then((choiceResult) => {
          this.loading = false;
          if (choiceResult.outcome !== 'accepted') {
            this.successMessage = '';
            this.errorMessage = 'You have declined the installation prompt';
          } else {
            this.errorMessage = '';
            this.successMessage = 'Application is being installed! Open it from your homescreen, app drawer, or notification area after the installation.';
            this.$router.replace({ name: 'Setup' });
          }
        });
      } else {
        this.disabled = true;
        this.successMessage = '';
        this.errorMessage = 'Something went wrong. Is the app already installed? If that is not the case, then you have to manually install the application. See the options below.'
      }
    },
    noInstall() {
      window.localStorage.setItem('noInstall', 'true');
      this.$router.replace({ name: 'Setup' });
    }
  }
});
